(function() {
  'use strict';

  angular
    .module('moneyMonitoring')
    .controller('RecordController', RecordController);

  RecordController.$inject = ['$scope', '$state', '$stateParams', '$mdDialog', 'dashboardApi', 'flashMethods', 'moment', '_'];

  function RecordController($scope, $state, $stateParams, $mdDialog, dashboardApi, flashMethods, moment, _) {

    var
      vm = this,
      m  = moment(),
      defeult_total;

    vm.title                 = $stateParams.recordId ? 'Редактировать запись' : 'Создать запись';
    vm.submit_btn            = $stateParams.recordId ? 'Сохранить' : 'Создать';
    vm.add_total_input       = $stateParams.recordId ? true : false;
    vm.months                = moment.months();
    vm.years                 = [2017, 2018, 2019, 2020, 2021, 2022, 2023, 2024, 2025, 2026];
    vm.current_months_index  = m.month();
    vm.new_record            = {};

    if (!$stateParams.recordId) {
      vm.new_record.month          = vm.months[vm.current_months_index];
      vm.new_record.days_in_month  = m.daysInMonth();
      vm.new_record.year           = m.year();
    } else {
      dashboardApi.getRecord($stateParams.recordId).then(
        function(response) {
          vm.new_record = response || {};
          defeult_total = vm.new_record.total;
        },
        function(response) {
          flashMethods.alertError('Извините, произошла ошибка!');
        }
      );
    }

    vm.update_total = function() {
      vm.new_record.total = vm.add_to_total ? (vm.new_record.total + vm.add_to_total) : defeult_total;
    }

    vm.save_record = function() {
      vm.recordForm.$setSubmitted();

      if (vm.recordForm.$invalid) return;

      var m = moment(vm.new_record.year + '-' + (vm.months.indexOf(vm.new_record.month) + 1), 'YYYY-MM');

      vm.new_record.days_in_month  = m.daysInMonth();
      vm.new_record.sum_in_day     = (vm.new_record.total / vm.new_record.days_in_month).toFixed(2);

      if ($stateParams.recordId) {
        dashboardApi.updateRecord($stateParams.recordId, vm.new_record).then(
          function(response) {
            defeult_total   = vm.new_record.total;
            vm.add_to_total = null;
            flashMethods.alertSuccess('Запись была успешно обновлена!');
          },
          function(response) {
            flashMethods.alertError('Извините, произошла ошибка!');
          }
        );
      } else {
        vm.new_record.created_at = m.startOf('month').toISOString();

        dashboardApi.addRecord(vm.new_record).then(
          function(response) {
            vm.back();
            flashMethods.alertSuccess('Запись была успешно создана!');
          },
          function(response) {
            flashMethods.alertError('Извините, произошла ошибка!');
          }
        );
      }

    }

    vm.back = function() {
      $state.go('dashboard', null, {reload: true});
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('moneyMonitoring')
    .controller('DeductionsListController', DeductionsListController);

  DeductionsListController.$inject = ['$scope', '$state', '$stateParams', '$mdDialog', 'dashboardApi', 'flashMethods', 'userMarks', 'moment',  'toArray', 'defaultDateFormat', '_'];

  function DeductionsListController($scope, $state, $stateParams, $mdDialog, dashboardApi, flashMethods, userMarks, moment, toArray, defaultDateFormat, _) {

    var
      vm = this;

    vm.list         = [];
    vm.marks_list   = [];
    vm.search_term  = '';
    vm.current_mark = null;
    vm.current_sort = {
      column: 'created_at'
    };
    vm.columns     = [
      {
        name: 'created_at',
        label: 'Дата',
        type: 'date'
      },
      {
        name: 'mark',
        label: 'Метка',
        type: 'string'
      },
      {
        name: 'description',
        label: 'Описание',
        type: 'string'
      },
      {
        name: 'amount',
        label: 'Сумма',
        type: 'integer',
        sort: true
      }
    ];

    vm.searchParams = {
      searchTerm: '',
      marksList: {},
      format: defaultDateFormat
    };

    dashboardApi.getRecord($stateParams.recordId).then(
      function(response) {
        vm.list =  toArray.create(response.deductions);
        vm.title = 'Список вычетов на ' + response.month;
      },
      function(response) {
        flashMethods.alertError('Извините, произошла ошибка!');
      }
    );

    userMarks.list().then(
      function(response) {
        vm.marks_list               = response || {};
        vm.searchParams.marksList   = vm.marks_list;
      },
      function() {
        flashMethods.alertError('Извините, произошла ошибка!');
      }
    );

    vm.apply_sort = function(columnName) {
      if (vm.current_sort.name !== columnName)
        vm.current_sort.type   = '';

      vm.current_sort.type    = vm.current_sort.type ? (vm.current_sort.type === 'asc' ? 'desc': '') : 'asc';
      vm.current_sort.column  = vm.current_sort.type === 'desc' ? '-' + columnName : columnName;
      vm.current_sort.name    = columnName;

      if (!vm.current_sort.type)
        vm.current_sort.column = 'created_at';
    }

    vm.remove_deduction = function(deductionId, ev) {
      var
        confirm = $mdDialog.confirm()
            .title('Удаление вычета')
            .textContent('Вы уверены, что хотите удалить данный вычет?')
            .targetEvent(ev)
            .ok('Удалить')
            .cancel('Нет');

      $mdDialog.show(confirm).then(
        function() {
          dashboardApi.removeDeduction($stateParams.recordId, deductionId).then(
            function(response) {
              vm.list = _.reject(vm.list, {id: deductionId});
              flashMethods.alertSuccess('Вычет успешно удален!');
            },
            function(response) {
              flashMethods.alertError('Извините, произошла ошибка!');
            }
          );
        }
      );
    }

    vm.searchFilter = function(params) {
      return function(item) {
        if (!params.searchTerm) return true;
        var result = [], mark, m, date;

        _.each(item, function(value, key) {
          if (key === 'mark') {
            mark = params.marksList[value];
            if (mark) {
              if (~mark.name.toLowerCase().indexOf(params.searchTerm.toLowerCase()))
                result.push(key);
            }
          } else if (key === 'created_at') {
            m     = moment(value),
            date  = m.isValid() ? m.format(params.format) : value;

            if (~date.indexOf(params.searchTerm))
              result.push(key);
          } else {
            if (~value.toString().indexOf(params.searchTerm))
              result.push(key);
          }
        });

        return result.length;
      }
    }

    vm.back = function() {
      $state.go('dashboard', null, {reload: true});
    }

    vm.add_deduction = function() {
      $state.go('dashboard.deduction', {recordId: $stateParams.recordId});
    }

    vm.edit_deduction = function(deductionId) {
      $state.go('dashboard.deduction', {recordId: $stateParams.recordId, deductionId: deductionId});
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('moneyMonitoring')
    .controller('DeductionController', DeductionController);

  DeductionController.$inject = ['$scope', '$state', '$stateParams', '$mdDialog', 'dashboardApi', 'flashMethods', 'moment', 'userMarks', '_'];

  function DeductionController($scope, $state, $stateParams, $mdDialog, dashboardApi, flashMethods, moment, userMarks, _) {

    var
      vm = this;

    vm.title           = $stateParams.deductionId ? 'Редактировать вычет' : 'Создать вычет';
    vm.submit_btn      = $stateParams.deductionId ? 'Сохранить' : 'Создать';
    vm.deduction       = {};
    vm.marks_list      = {};
    vm.from_state      = '';
    vm.from_params     = null;

    vm.back = function() {
      $state.go(vm.from_state, vm.from_params, {reload: true});
    }

    vm.set_deduction_date = function() {
      vm.deduction.created_at = vm.picker_date.toISOString();
    }

    if (!$stateParams.deductionId) {
      vm.picker_date     = new Date();
      vm.picker_min_date = moment().startOf('month')._d;
      vm.picker_max_date = moment().endOf('month')._d;

      vm.set_deduction_date();
    }

    $scope.$on('$stateChangeSuccess', function(ev, toState, toParams, fromState, fromParams) {
      vm.from_state = fromState.name || 'dashboard';
      vm.from_params = toParams.recordId ? {recordId: toParams.recordId} : null;
    });

    if ($stateParams.recordId && $stateParams.deductionId) {
      dashboardApi.getDeduction($stateParams.recordId, $stateParams.deductionId).then(
        function(response) {
          vm.deduction       = response || {};
          vm.picker_date     = moment(vm.deduction.created_at)._d;
          vm.picker_min_date = moment(vm.deduction.created_at).startOf('month')._d;
          vm.picker_max_date = moment(vm.deduction.created_at).endOf('month')._d;
        },
        function(response) {
          flashMethods.alertError('Извините, произошла ошибка!');
        }
      );
    }

    userMarks.list().then(
      function(response) {
        vm.marks_list = response || {};
      },
      function() {
        flashMethods.alertError('Извините, произошла ошибка!');
      }
    );

    vm.save_deduction = function() {
      vm.deductionForm.$setSubmitted();

      if (vm.deductionForm.$invalid) return;

      if ($stateParams.deductionId) {
        dashboardApi.updateDeduction($stateParams.recordId, $stateParams.deductionId, vm.deduction).then(
          function(response) {
            flashMethods.alertSuccess('Вычет успешно обновлен!');
          },
          function() {
            flashMethods.alertError('Извините, произошла ошибка!');
          }
        );
      } else {
        dashboardApi.createDeduction($stateParams.recordId, vm.deduction).then(
          function(response) {
            vm.deduction = {};
            vm.deductionForm.$setPristine();
            vm.deductionForm.$setUntouched();
            flashMethods.alertSuccess('Вычет успешно создан!');
          },
          function() {
            flashMethods.alertError('Извините, произошла ошибка!');
          }
        );
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('moneyMonitoring')
    .controller('DashboardController', DashboardController);

  DashboardController.$inject = ['$scope', '$state', '$mdDialog', 'dashboardApi', 'flashMethods', 'moment', 'toArray', '_'];

  function DashboardController($scope, $state, $mdDialog, dashboardApi, flashMethods, moment, toArray, _) {

    var
      vm = this,
      m  = moment();

    vm.list            = [];
    vm.items_per_page  = 12;
    vm.search_term     = '';
    vm.current_sort    = {
      column: 'created_at'
    };

    vm.columns = [
      {
        name: 'created_at',
        label: 'Дата',
        type: 'date',
        format: 'MMMM YYYY'
      },
      {
        name: 'total',
        label: 'Общая сумма',
        type: 'integer',
        sort: true
      },
      {
        name: 'sum_amounts',
        label: 'Всего потрачено',
        type: 'integer',
        sort: true
      },
      {
        name: 'sum_in_day',
        label: 'Сумма в день',
        type: 'integer'
      }
    ];

    dashboardApi.list().then(
      function(response) {
        vm.list = toArray.create(response);
        vm.list = _.each(vm.list, function(item) {
          item.sum_amounts = vm.get_sum_amounts(item.deductions);
        });
      },
      function(response) {
        flashMethods.alertError('Извините, произошла ошибка!');
      }
    );

    vm.apply_sort = function(columnName) {
      if (vm.current_sort.name !== columnName)
        vm.current_sort.type   = '';

      vm.current_sort.type    = vm.current_sort.type ? (vm.current_sort.type === 'asc' ? 'desc': '') : 'asc';
      vm.current_sort.column  = vm.current_sort.type === 'desc' ? '-' + columnName : columnName;
      vm.current_sort.name    = columnName;

      if (!vm.current_sort.type)
        vm.current_sort.column = 'created_at';
    }

    vm.get_sum_amounts = function(list) {
      var keys = _.pluck(list, 'amount');
      return _.reduce(keys, function(memo, num){ return memo + num; }, 0);
    }

    vm.add_record = function() {
      $state.go('dashboard.record');
    }

    vm.edit_record = function(recordId) {
      $state.go('dashboard.record', {recordId: recordId});
    }

    vm.add_deduction = function(recordId) {
      $state.go('dashboard.deduction', {recordId: recordId});
    }

    vm.deductions_list = function(recordId) {
      $state.go('dashboard.deductions_list', {recordId: recordId});
    }

    vm.close_dialog = function() {
      $mdDialog.cancel();
    }

    vm.remove_record = function(recordId, ev) {
      var
        confirm = $mdDialog.confirm()
            .title('Удаление записи')
            .textContent('Вы уверены, что хотите удалить запись?')
            .targetEvent(ev)
            .ok('Удалить')
            .cancel('Нет');

      $mdDialog.show(confirm).then(
        function() {
          dashboardApi.removeRecord(recordId).then(
            function(response) {
              vm.list = _.reject(vm.list, {id: recordId});
              flashMethods.alertSuccess('Запись была успешно удалена!');
            },
            function(response) {
              flashMethods.alertError('Извините, произошла ошибка!');
            }
          );
        }
      );
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('moneyMonitoring')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider) {
    $stateProvider
      .state('sign_in', {
        url: '/sign_in',
        templateUrl: 'app/auth/sign_in/partials/sign_in.html',
        controller: 'SignInController',
        controllerAs: 'sign_in'
      })
      .state('sign_up', {
        url: '/sign_up',
        templateUrl: 'app/auth/sign_up/partials/sign_up.html',
        controller: 'SignUpController',
        controllerAs: 'sign_up'
      })
      .state('confirmation_email', {
        url: '/sign_up/confirmation_email',
        templateUrl: 'app/auth/sign_up/partials/confirmation_email.html',
        resolve: {
          isEmail: function($state, $timeout, $cookies, $q) {
            var deferred = $q.defer();

            if ($cookies.get('confirmation_send')) {
              deferred.resolve();
            } else {
              deferred.reject();
              $timeout(function() { $state.go('home') }, 0);
            }
            return deferred.promise;
          }
        }
      })
      .state('verify_email', {
        url: '/sign_up/verify_email',
        templateUrl: 'app/auth/sign_up/partials/verify_email.html',
        controller: 'SignUpController',
        controllerAs: 'sign_up',
        resolve: {
          verifyEmail: function($state, $timeout, $cookies, $location, $q) {
            var
              deferred   = $q.defer(),
              url_params = $location.search(),
              mode       = url_params.mode;

            if (url_params.oobCode && mode === 'verifyEmail') {
              $cookies.remove('confirmation_send');
              deferred.resolve();
            } else {
              deferred.reject();
              $timeout(function() { $state.go('home') }, 0);
            }
            return deferred.promise;
          }
        }
      })
      .state('confirm_email', {
        url: '/confirm_email',
        templateUrl: 'app/auth/forgot_password/partials/confirm_email.html',
        controller: 'ForgotPasswordController',
        controllerAs: 'forgot_password'
      })
      .state('confirm_password', {
        url: '/change_password',
        templateUrl: 'app/auth/forgot_password/partials/confirm_password.html',
        controller: 'ForgotPasswordController',
        controllerAs: 'forgot_password',
        resolve: {
          checkCode: function($state, $timeout, $location, $q) {
            var
              deferred   = $q.defer(),
              url_params = $location.search(),
              mode       = url_params.mode;

            if (url_params.oobCode && mode === 'resetPassword') {
              deferred.resolve();
            } else {
              deferred.reject();
              $timeout(function() { $state.go('home') }, 0);
            }
            return deferred.promise;
          }
        }
      })
      .state('confirm_email_success', {
        url: '/confirm_email_success',
        templateUrl: 'app/auth/forgot_password/partials/confirm_email_success.html',
        resolve: {
          checkParamsCode: function($state, $timeout, $cookies, $q) {
            var deferred = $q.defer();

            if ($cookies.get('email_confirmation')) {
              deferred.resolve();
            } else {
              deferred.reject();
              $timeout(function() { $state.go('home') }, 0);
            }
            return deferred.promise;
          }
        }
      })
      .state('change_password_success', {
        url: '/change_password_success',
        templateUrl: 'app/auth/forgot_password/partials/change_success.html',
        resolve: {
          checkParamsCode: function($state, $timeout, $cookies, $q) {
            var deferred = $q.defer();

            if ($cookies.get('change_password')) {
              $cookies.remove('email_confirmation');
              deferred.resolve();
            } else {
              deferred.reject();
              $timeout(function() { $state.go('home') }, 0);
            }
            return deferred.promise;
          }
        }
      });
  }

})();

(function() {
  'use strict';

  angular
    .module('moneyMonitoring')
    .run(runBlock);

  /** @ngInject */
  function runBlock($rootScope, $state, Auth, appConfig) {

    $rootScope.preloaderClass = false;

    $rootScope.preloaderActiveClass = function(bool) {
      $rootScope.preloaderClass = bool;
    }

    $rootScope.headerHide = true;
    $rootScope.states_for_anonymous = ['home', 'sign_in', 'sign_up', 'confirmation_email', 'verify_email', 'confirm_email',
                                      'confirm_email_success', 'confirm_password', 'change_password_success', 'auth_action'];

    $rootScope.$on('$stateChangeSuccess', function(ev, toState) {
      $rootScope.headerHide = _.contains($rootScope.states_for_anonymous, toState.name);

      if (appConfig.user) {
        if ($rootScope.headerHide) {
          ev.preventDefault();
          $state.go('dashboard');
        }
      } else {
        if (!$rootScope.headerHide) {
          ev.preventDefault();
          $state.go('logout');
        }
      }
    });

  }

})();

(function() {
  'use strict';

  angular
    .module('moneyMonitoring')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/auth/auth.html'
      })
      .state('logout', {
        url: '/logout',
        resolve: {
          userLogout: function($state, Auth) {
            // Auth.removeToken();
            Auth.logout().then(
              function(response) {
                $state.go('sign_in');
              },
              function(response) {
                $state.go('sign_in');
              }
            )
          }
        }
      })
      .state('auth_action', {
        url: '/auth_action',
        resolve: {
          redirect: function($state, $timeout, $location, $cookies, $q, $window) {
            var
              deferred      = $q.defer(),
              url_params    = $location.search(),
              absUrl        = $location.absUrl(),
              path          = $location.path(),
              mode          = url_params.mode,
              redirect_url  = '',
              url           = '';

            $timeout(function() {
              if (mode) {
                if (mode === 'verifyEmail')
                  redirect_url = '/sign_up/verify_email';
                else if (mode === 'resetPassword')
                  redirect_url = '/change_password';

                url = absUrl.replace(path, redirect_url);

                $window.location.href = url;
              } else {
                $state.go('home');
              }
            }, 0);

            deferred.reject();
            return deferred.promise;
          }
        }
      });

    $urlRouterProvider.otherwise(function($ingector) {
      var
        $state    = $ingector.get('$state'),
        appConfig = $ingector.get('appConfig'),
        redirect  = $state.current.name || (appConfig.user ? 'dashboard' : 'home');

      $state.go(redirect);
    });
  }

})();

(function() {
  'use strict';

  angular
    .module('moneyMonitoring')
    .controller('IndexController', IndexController);

  IndexController.$inject = ['$scope', '$timeout', '$mdComponentRegistry'];

  function IndexController($scope, $timeout, $mdComponentRegistry) {

    var vm = this;

    $scope.mdSidenavInit = function() {
      $scope.toggle = angular.noop;
      $scope.isOpen = angular.noop;

      $mdComponentRegistry
        .when('sidebar-left')
        .then(function(sideNav){
          $scope.isOpen = angular.bind(sideNav, sideNav.isOpen );
          $scope.toggle = angular.bind(sideNav, sideNav.toggle );
        });
    }

    $scope.$on('login_animation', function() {
        vm.login_animation = true;
        $timeout(function() {
          vm.login_animation = false;
        }, 2000);
    });

  }

})();

